'use client';


import React, { useState } from "react";
import Image from "next/image";
import { Button } from "./button";
import Datepicker from "./datepicker";
import GooglePlacesAutocomplete from "../GooglePlacesAutocomplete";

const Header: React.FC = () => {
  const [pickup, setPickup] = useState<string>("");
  const [dropoff, setDropoff] = useState<string>("");
  const [price, setPrice] = useState<number>(0);

  const handlePlaceChange = (type: "pickup" | "dropoff") => (place: google.maps.places.PlaceResult | null) => {
    const address = place?.formatted_address || "";
    if (type === "pickup") setPickup(address);
    if (type === "dropoff") setDropoff(address);

    // Example logic for setting price (could be based on real API or distance)
    if (pickup && dropoff) {
      setPrice(50); // Replace with actual pricing logic
    }
  };

  return (
    <div className="w-[1240px] h-48 relative mx-auto">
      {/* ...logo area stays same... */}

      <div className="w-[1266px] px-12 py-9 bg-white shadow-[0px_4px_10px_0px_rgba(218,6,6,0.10)] flex flex-col justify-center items-center gap-2.5">
        <div className="inline-flex justify-center items-center gap-32">
          <div className="flex justify-start items-center gap-16">
            {/* Pickup */}
            <div className="inline-flex flex-col">
              <div className="p-2.5 text-neutral-500 text-base">Pickup</div>
              <GooglePlacesAutocomplete placeholder="Enter pickup location" onPlaceChanged={handlePlaceChange("pickup")} />
            </div>
            {/* Dropoff */}
            <div className="inline-flex flex-col">
              <div className="p-2.5 text-neutral-500 text-base">Drop Off</div>
              <GooglePlacesAutocomplete placeholder="Enter drop off location" onPlaceChanged={handlePlaceChange("dropoff")} />
            </div>
            {/* Datepicker */}
            <div className="inline-flex flex-col">
              <div className="p-2.5 text-neutral-500 text-base">Date Range</div>
              <div className="p-2.5">
                <Datepicker />
              </div>
            </div>
            {/* Price */}
            <div className="inline-flex flex-col">
              <div className="p-2.5 text-neutral-500 text-base">Pricing</div>
              <div className="text-zinc-600 text-base p-2.5">${price.toFixed(2)}</div>
            </div>
          </div>
          {/* Button */}
          <Button variant="destructive" size="lg" className="font-bold px-8">
            Rent Now
          </Button>
        </div>
      </div>
    </div>
  );
};

export default Header;
